import React, { useEffect, useState } from 'react';
import { Table, Typography, Skeleton, Alert, Button, Tag, message } from 'antd';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import type { ColumnType } from 'antd/es/table';
import { isoToDate } from '@app/utils/utils';

interface Record {
  id: string;
  household_id: string;
  caseworker_name: string;
  caregiver_name: string;
  facility: string;
  vca_id: string;
  comment: string;
  verifier: string;
  status: string;
  date_created: string;
}

const FlagRecordPage: React.FC = () => {
  const [loadingTable, setLoadingTable] = useState(true);
  const [records, setRecords] = useState<Record[]>([]);
  const [error, setError] = useState<string | null>(null);
  const navigate = useNavigate();

  const handleExport = () => {
    console.log('Exporting records...');
    try {
      const csvContent =
        'data:text/csv;charset=utf-8,' +
        ['Household ID,Caseworker Name,Caregiver Name,Facility,Comment,Verifier,Status,Created At']
          .concat(
            records.map(
              (record) =>
                `${record.household_id},${record.caseworker_name},${record.caregiver_name},${record.facility},${record.comment},${record.verifier},${record.status},${record.date_created}`, // Use `date_created` here
            ),
          )
          .join('\n');
      const encodedUri = encodeURI(csvContent);
      const link = document.createElement('a');
      link.setAttribute('href', encodedUri);
      link.setAttribute('download', 'district_flagged_records.csv');
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
      message.success('Exported successfully!');
      console.log('Export successful!');
    } catch (error) {
      console.error('Error exporting data:', error);
      message.error('Failed to export records.');
    }
  };
  useEffect(() => {
    console.log('Fetching records...');
    const fetchTableData = async () => {
      try {
        setLoadingTable(true);
        const response = await axios.get(`${process.env.REACT_APP_BASE_URL}/items/flagged_forms_ecapplus_pmp`, {
          headers: { Authorization: `Bearer ${localStorage.getItem('access_token')}` },
        });
        console.log('Records fetched:', response.data.data); // Log the fetched data
        setRecords(response.data.data);
      } catch (err) {
        console.error('Error fetching table data:', err);
        setError('Failed to fetch table data.');
      } finally {
        setLoadingTable(false);
      }
    };

    fetchTableData();
  }, []);

  const columns: ColumnType<Record>[] = [
    {
      title: 'Household ID',
      dataIndex: 'household_id',
      key: 'household_id',
    },
    {
      title: 'Caseworker Name',
      dataIndex: 'caseworker_name',
      key: 'caseworker_name',
    },
    {
      title: 'Caregiver Name',
      dataIndex: 'caregiver_name',
      key: 'caregiver_name',
    },
    {
      title: 'Facility',
      dataIndex: 'facility',
      key: 'facility',
    },
    {
      title: 'Comment',
      dataIndex: 'comment',
      key: 'comment',
    },
    {
      title: 'Verifier',
      dataIndex: 'verifier',
      key: 'verifier',
    },
    {
      title: 'Status',
      dataIndex: 'status',
      key: 'status',
      render: (text: string) => (
        <Tag color={text === 'Pending' ? 'orange' : text === 'Approved' ? 'green' : 'red'}>{text.toUpperCase()}</Tag>
      ),
    },
    {
      title: 'Created At',
      dataIndex: 'date_created',
      key: 'date_created',
      render: (text: string | null) => {
        if (text) {
          const date = isoToDate(text);
          return date.toLocaleDateString();
        } else {
          return 'Not Applicable';
        }
      },
    },
  ];

  // Function to handle row click
  const handleRowClick = (record: Record) => {
    return {
      onClick: () => {
        navigate(`/profile/household-profile/${encodeURIComponent(record.household_id)}`, {
          state: { household: record }, // Pass the full household object
        });
      },
      style: { cursor: 'pointer' },
    };
  };

  return (
    <>
      <Typography.Title level={4}>District Flagged Records</Typography.Title>

      {error && <Alert message="Error" description={error} type="error" showIcon style={{ marginBottom: '20px' }} />}

      <Button type="primary" onClick={handleExport} style={{ marginBottom: '20px' }}>
        Export to CSV
      </Button>

      {loadingTable ? (
        <Skeleton active paragraph={{ rows: 3 }} />
      ) : (
        <Table
          scroll={{ x: 500 }}
          dataSource={records}
          columns={columns}
          rowKey="id"
          pagination={{ pageSize: 10 }}
          onRow={handleRowClick}
        />
      )}
    </>
  );
};

export default FlagRecordPage;
